import React from 'react';
import { useNavigate } from 'react-router-dom';

const PagesNotFound = () => {
    const navigate = useNavigate();
    const navigateToLogin =()=>{
        navigate("/login")
    }
    return (
        <div style={{width:"100%", height:"100vh",backgroundColor:"#cfcfcf", display:"flex", alignItems:"center", justifyContent:"center"}}>
            <h1 style={{fontSize:82}}>Pages Not Found </h1>
            <button className='button-login' onClick={navigateToLogin} >Going to Login</button>
        </div>
    );
}

export default PagesNotFound;
